import { Component, OnInit } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';
import { NzMessageService } from 'ng-zorro-antd';
import { Pegawai_card } from 'src/app/sdk/hr/models';
import { PegawaiDetailService } from '../../../service/pegawai-detail.service';
import { DirektoriPegawaiCardService } from 'src/app/sdk/hr/services';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private direktoriPegawaiCardService: DirektoriPegawaiCardService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private pegawaiDetailService: PegawaiDetailService,
        private nzMessageService: NzMessageService
    ) { }

    id_pegawai: string = ''
    data: Pegawai_card[] = []

    ngOnInit() {
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
        })
        this.getData()
    }


    getData() {
        this.direktoriPegawaiCardService.getDirektoriPegawaiCards(JSON.stringify({ id_pegawai: this.id_pegawai })).subscribe(
            data => {
                this.data = data.data
            }
        )
    }

    delete(id) {
        this.spinner.show()
        this.direktoriPegawaiCardService
            .deleteDirektoriPegawaiCardsId(id)
            .subscribe(data => {
                this.getData()
                this.spinner.hide()
            })

    }

    batalDelete() {
        this.nzMessageService.info('click cancel')
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/detail-pelamar', this.id_pegawai, 'id-cards'])
    }

}
