import { Component, OnInit } from '@angular/core';
import { NgxSpinnerService } from 'ngx-spinner';
import { Router } from '@angular/router';
import { PelamarServiceService } from '../../service/pelamar-service.service';
import { NzMessageService } from 'ng-zorro-antd';
import { Pelamar_pengalaman, Pelamar_idcard } from 'src/app/sdk/hr/models';
import { DirektoriPelamarIdcardService } from 'src/app/sdk/hr/services/direktori-pelamar-idcard.service';

@Component({
  selector: 'app-list',
  templateUrl: './list.component.html',
  styleUrls: ['./list.component.scss']
})
export class ListComponent implements OnInit {

    constructor(
        private direktoriPelamarIdcardService: DirektoriPelamarIdcardService,
        private spinner: NgxSpinnerService,
        private router: Router,
        private pelamarServiceService: PelamarServiceService,
        private nzMessageService: NzMessageService
    ) { }

    id_pelamar: string = ''
    data: Pelamar_idcard[] = []

    ngOnInit() {
        this.pelamarServiceService.getIdPelamar().subscribe(data => {
            this.id_pelamar = data
        })
        this.getData()
    }


    getData() {
        this.direktoriPelamarIdcardService.getDirektoriPelamarIdcards(JSON.stringify({ id_pelamar: this.id_pelamar })).subscribe(
            data => {
                this.data = data.data
            }
        )
    }

    delete(id) {
        this.spinner.show()
        this.direktoriPelamarIdcardService
            .deleteDirektoriPelamarIdcardsId(id)
            .subscribe(data => {
                this.getData()
                this.spinner.hide()
            })

    }

    batalDelete() {
        this.nzMessageService.info('click cancel')
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/detail-pelamar', this.id_pelamar, 'id-cards'])
    }

}
