import { Component, OnInit } from '@angular/core';
import { DirektoriPelamarIdcardService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PelamarServiceService } from '../../service/pelamar-service.service';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Pelamar_idcard } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';
import { SingleUploadService } from 'src/app/components/document/single-upload.service';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private direktoriPelamarIdcardService: DirektoriPelamarIdcardService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pelamarServiceService: PelamarServiceService,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private singleUploadService: SingleUploadService
    ) { }

    toogleUpload = false
    id_pelamar: string = ''
    id_pelamar_id_card: string = ''
    data: Pelamar_idcard

    ngOnInit() {
        this.id_pelamar_id_card = this.activatedRoute.snapshot.paramMap.get('id')
        this.pelamarServiceService.getIdPelamar().subscribe(data => {
            this.id_pelamar = data
            console.log('id pegawai paja tu ' + this.id_pelamar)
        })
        this.form()
        this.getData()

        this.singleUploadService.getCallback().subscribe(
            data => {

                if (data == 'pelamar_idcard.finish') {
                    this.toogleUpload = false;
                }
            }
        )

        this.singleUploadService.getId().subscribe(
            data => {
                if (data) {
                    this.dataForm.patchValue({ id_dokumen: data })
                    this.simpan()
                }
            }
        )
    }

    togleShowUpload() {

        this.toogleUpload = true;
    }

    getData() {
        this.spinner.show()
        this.direktoriPelamarIdcardService.getDirektoriPelamarIdcardsId(this.id_pelamar_id_card).subscribe(
            data=> {
                this.dataForm.setValue(data.data)
                this.spinner.hide()

                this.singleUploadService.param.next({
                    for_module: "pelamar_idcard",
                    for_table: "erp_hr_pelamar.pelamar_idcard",
                    for_module_id: data.data.id_pelamar_id_card
                })
            }
        )
    }

    dataForm: FormGroupTypeSafe<Pelamar_idcard>
    form() {
        this.dataForm = this.fb.group<Pelamar_idcard>({
            jenis: new FormControl('', [Validators.required]),
            tanggal_berlaku: new FormControl(''),
            tanggal_expired: new FormControl(''),
            id_number: new FormControl(''),
            id_dokumen: new FormControl(''),
            id_pelamar_id_card: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
            id_pelamar: new FormControl(this.id_pelamar),
        })
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.data = this.dataForm.value
        this.direktoriPelamarIdcardService
            .putDirektoriPelamarIdcardsId({id: this.id_pelamar_id_card, body: this.data})
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/detail-pelamar', this.id_pelamar, 'id-card'])
    }
}
