import { Component, OnInit } from '@angular/core';
import { DirektoriPelamarIdcardService } from 'src/app/sdk/hr/services';
import { Pelamar_idcard } from 'src/app/sdk/hr/models';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';
import { PelamarServiceService } from '../../service/pelamar-service.service';
import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router } from '@angular/router';
import { FormControl, Validators } from '@angular/forms';

@Component({
  selector: 'app-add',
  templateUrl: './add.component.html',
  styleUrls: ['./add.component.scss']
})
export class AddComponent implements OnInit {

    constructor(
        private direktoriPelamarIdcardService: DirektoriPelamarIdcardService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pelamarServiceService: PelamarServiceService,
        private pesan: PesanService,
        private router: Router,
    ) {}

    id_pelamar: string = ''
    data: Pelamar_idcard

    ngOnInit() {
        this.pelamarServiceService.getIdPelamar().subscribe(data => {
            this.id_pelamar = data
            console.log('id pegawai paja tu ' + this.id_pelamar)
        })
        this.form()
    }

    dataForm: FormGroupTypeSafe<Pelamar_idcard>
    form() {
        this.dataForm = this.fb.group<Pelamar_idcard>({
            jenis: new FormControl('', [Validators.required]),
            tanggal_berlaku: new FormControl(''),
            tanggal_expired: new FormControl(''),
            id_number: new FormControl(''),
            id_pelamar: new FormControl(this.id_pelamar),
        })
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.data = this.dataForm.value
        this.direktoriPelamarIdcardService
            .postDirektoriPelamarIdcards(this.data)
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/detail-pelamar', this.id_pelamar, 'id-card'])
    }

}
