import { Component, OnInit } from '@angular/core';
import { DirektoriPelamarIdcardService, DirektoriPegawaiCardService } from 'src/app/sdk/hr/services';
import { NgxSpinnerService } from 'ngx-spinner';
import { NzNotificationService } from 'ng-zorro-antd';
import { FormBuilderTypeSafe, FormGroupTypeSafe } from 'src/app/services/angular-reactive-forms-helper';

import { PesanService } from 'src/app/services/tool/pesan.service';
import { Router, ActivatedRoute } from '@angular/router';
import { Pelamar_idcard, Pegawai_card } from 'src/app/sdk/hr/models';
import { FormControl, Validators } from '@angular/forms';
import { SingleUploadService } from 'src/app/components/document/single-upload.service';
import { PegawaiDetailService } from '../../../service/pegawai-detail.service';

@Component({
  selector: 'app-edit',
  templateUrl: './edit.component.html',
  styleUrls: ['./edit.component.scss']
})
export class EditComponent implements OnInit {

    constructor(
        private direktoriPegawaiCardService: DirektoriPegawaiCardService,
        private spinner: NgxSpinnerService,
        private notification: NzNotificationService,
        private fb: FormBuilderTypeSafe,
        private pegawaiDetailService: PegawaiDetailService,
        private pesan: PesanService,
        private router: Router,
        private activatedRoute: ActivatedRoute,
        private singleUploadService: SingleUploadService
    ) { }

    toogleUpload = false
    id_pegawai: string = ''
    id_pegawai_card: string = ''
    data: Pegawai_card

    ngOnInit() {
        this.id_pegawai_card = this.activatedRoute.snapshot.paramMap.get('id')
        this.pegawaiDetailService.getIdPegawai().subscribe(data => {
            this.id_pegawai = data
            console.log('id pegawai paja tu ' + this.id_pegawai)
        })
        this.form()
        this.getData()

        this.singleUploadService.getCallback().subscribe(
            data => {

                if (data == 'pelamar_idcard.finish') {
                    this.toogleUpload = false;
                }
            }
        )

        this.singleUploadService.getId().subscribe(
            data => {
                if (data) {
                    this.dataForm.patchValue({ id_dokumen: data })
                    this.simpan()
                }
            }
        )
    }

    togleShowUpload() {

        this.toogleUpload = true;
    }

    getData() {
        this.spinner.show()
        this.direktoriPegawaiCardService.getDirektoriPegawaiCardsId(this.id_pegawai_card).subscribe(
            data=> {
                this.dataForm.setValue(data.data)
                this.spinner.hide()

                this.singleUploadService.param.next({
                    for_module: "pegawai_card",
                    for_table: "erp_hr.pegawai_idcard",
                    for_module_id: data.data.id_pegawai_card
                })
            }
        )
    }

    dataForm: FormGroupTypeSafe<Pegawai_card>
    form() {
        this.dataForm = this.fb.group<Pegawai_card>({
            jenis: new FormControl('', [Validators.required]),
            tanggal_berlaku: new FormControl(''),
            tanggal_expired: new FormControl(''),
            id_number: new FormControl(''),
            id_dokumen: new FormControl(''),
            id_pegawai_card: new FormControl(''),
            created_at: new FormControl(''),
            updated_at: new FormControl(''),
            id_pegawai: new FormControl(this.id_pegawai),
        })
    }

    simpan() {
        this.spinner.show()
        if (this.dataForm.invalid) {
            this.pesan.pesanWarningForm('Form Tidak Valid')
            this.spinner.hide()
            return
        }

        this.data = this.dataForm.value
        this.direktoriPegawaiCardService
            .putDirektoriPegawaiCardsId({id: this.id_pegawai_card, body: this.data})
            .subscribe(
                data => {
                    this.goToBack()
                    this.spinner.hide()
                },
                err => {
                    this.pesan.pesanWarningForm('Gagal di simpan')
                    this.spinner.hide()
                },
                () => {
                    console.log('HTTP request completed.')
                },
            )
    }

    goToBack() {
        this.router.navigate(['/hr/direktori/employee/pegawai-detail', this.id_pegawai,'biodata', 'id-card', 'list'])
    }
}
